

























import { getUserInfo } from "@/utils/userAxios";

export default {
  data() {
    return {
      userData: {},
    };
  },

  created() {
    this.getUserInfo();
  },

  methods: {
    // ユーザ情報を取得
    async getUserInfo() {
      const res = await getUserInfo().catch((err) => {
        return err.response;
      });
      // 取得したデータをdata()へ格納
      this.userData = res;
    },
  },
};
