<template>
  <v-container fluid class="text-center">
    <v-row justify="center" align-content="center">
      <v-card :tile="$vuetify.breakpoint.sm || $vuetify.breakpoint.xs" class="fill-width" flat max-width="640">
        <v-card-title class="pa-8">
          <h4 class="fill-width">ログイン</h4>
        </v-card-title>

        <v-divider> </v-divider>

        <AuthContents buttonValue="ログイン" :onSubmitFunction="loginCallBackFunction" :isVisibleErrmsg="isVisibleErrmsg"
          errMsgValue="メールアドレスまたはパスワードが違います" @form-data="addFormData" />

        <v-divider class="mb-4"> </v-divider>

        <div class="mb-8">
          <div>アカウントをお持ちでない方</div>
          <router-link to="/signup">会員登録に移動</router-link>
        </div>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import log from 'loglevel';
import AuthContents from "@/components/organisms/AuthContents.vue";

// リポジトリの生成
import { repositoryFactory } from "@/repository/RepositoryFactory";
const userRepository = repositoryFactory.get("user");

export default {
  components: {
    AuthContents,
  },

  data() {
    return {
      loginCallBackFunction: this.login,
      isVisibleErrmsg: false,
      formData: {},
    };
  },

  methods: {
    // 子のコンポーネントから値を受け取るためのメソッド
    addFormData(value) {
      this.formData = value;
    },

    // フォームに入力されたデータをapiへ送信する
    async login() {
      const formData = {
        email: this.formData.email,
        password: this.formData.password,
      };

      const res = await userRepository.login(formData).catch((err) => {
        return err.response;
      });
      if (res.status !== 200) {
        log.warn("ログインに失敗しました");
        log.info(res.data);
        this.showErrMsg();

        // 認証フラグをfalseにする
        await this.$store.dispatch("user/setAuth", false);
        log.info({ isLoginState: this.$store.getters.auth });
        return;
      }
      log.info("ログインに成功しました");
      log.info(res.data);

      // 認証フラグをtrueにする
      await this.$store.dispatch("user/setAuth", true);
      log.info({ isLoginState: this.$store.getters.auth });

      // リンクを移動
      await this.$router.push("/dashboard");
    },

    // templateにエラーメッセージを表示させる(フラグをtrueにすることで)
    showErrMsg() {
      this.isVisibleErrmsg = true;
    },
  },
};
</script>

<style lang="scss" scoped>
.fill-width {
  width: 100%;
}
</style>